import React, {Component} from 'react';
import {Link} from 'react-router';

class PostsList extends Component {
    componentWillMount() {
        this.props.fetchPosts();
    }

    renderPosts(posts) {
        return posts.map((post, index) => {
            return (
                <li className="post-group-item" key={post._id}>
                    <Link className="post-image" to={"posts/" + post._id}>
                        <img src="https://unsplash.it/157/200/?random" />
                    </Link>
                    <div className="post-content">
                        <h3><Link to={"posts/" + post._id}>
                            Inspiration for Letter Effects
                        </Link></h3>
                        <div>In <Link to={"posts/" + post._id}>Playground</Link>, Oct 18. 2016</div>
                        <h4>A set of inspirational letter animations for display typography effects powered by anime.js.</h4>
                    </div>
                </li>
            );
        });
    }

    render() {
        const {posts, loading, error} = this.props.postsList;

        if (loading) {
            return <div className="container"><h3>Loading...</h3></div>
        } else if(error) {
            return <div className="alert alert-danger">Error: {error.message}</div>
        }

        return (
            <ul className="post-group">
                {this.renderPosts(posts)}
            </ul>
        );
    }
}


export default PostsList;



